import React, { useContext } from 'react'
import { ModalContext } from './Modal'
import TaskForm from './TaskForm'
import { DATETIME_OPTIONS, shown_dateTime } from './DateTimeUtils'
import Icon from './Icon'

export default function ItemInList({t, options}) {
    // console.log('render item in list')
    // console.log(t)
    // set default options
    options = options || {}
    const dateTimeFormat = options.dateTimeFormat || DATETIME_OPTIONS.FULL
    const showDoneStatus = options.showDoneStatus || false

    const modal = useContext(ModalContext)
    const handleClick = () => {
        modal.setContent(
            <TaskForm oldTask={t} handleCloseForm={()=>{modal.setContent(null)}}/>
        )
    }

    const decorate_by_priority = (p) => {
        const high = 'text-info'
        const medium = ''
        const low = 'text-body-tertiary'
        if (p >= 6) {
            return high
        }
        else if (p >= 3) {
            return medium
        }
        else {
            return low
        }
    }
    const decorate_by_done = (doneDate) => {
        let content = null
        if (!showDoneStatus) {
            return null
        }
        else if (doneDate) {
            content = <Icon i='bi-check-square-fill'/>
        }
        else {
            content = <Icon i='bi-square'/>
        }
        return (
            <div className='me-3 align-self-center'>
                {content}
            </div>
        )
    }
    
    return (
        <div className='d-flex align-items-start pt-1 pb-3' onClick={handleClick}>
            {decorate_by_done(t.doneDate)}
            <div className={ ` lh-sm fs-5 me-auto `+ decorate_by_priority(t.priority) }>
                {t.taskName}
            </div>
            <div className='flex-shrink-0 text-body-tertiary'>
                {shown_dateTime(t.dueDate, dateTimeFormat)}
            </div>
        </div>
    )
}
